exports.push([module.id, "\n.wylib-mdew table {\n    border-collapse: collapse;\n    width: 100%;\n//    border: 1px solid blue;\n}\n.wylib-mdew table .label {\n    text-align: right;\n}\n.wylib-mdew table .titles {\n    width: 10%;\n}\n.wylib-mdew table tr:nth-child(even) .label {\n    background: #f0f0f0\n}\n.wylib-mdew td {\n    border: 1px solid #e8e8e8;\n    white-space: nowrap;\n}\n", "", {"version":3,"sources":["/home/kyle/share/devel/wylib/src/src/mdew.vue"],"names":[],"mappings":";AAoFA;IACA,0BAAA;IACA,YAAA;AACA,6BAAA;CACA;AACA;IACA,kBAAA;CACA;AACA;IACA,WAAA;CACA;AACA;IACA,mBAAA;CACA;AACA;IACA,0BAAA;IACA,oBAAA;CACA","file":"mdew.vue","sourcesContent":["//Multiple data field editing widget\n//Copyright WyattERP.org: GNU GPL Ver 3; see: License in root of this package\n// -----------------------------------------------------------------------------\n//TODO:\n//X- Can layout multiple dews in a grid\n//X- Start with a single column of value\n//- Can get rid of ref= now?\n//- \n//- Later:\n//- Can do 2D array with: \n//-   Columnspans\n//-   Piggybacks\n//-   Derived values\n//- \n\n<template>\n  <div class=\"wylib wylib-mdew\">\n    <table class=\"wylib-mdew-nodrag\">\n      <colgroup><col class=\"titles\"/></colgroup>\n      <tr v-for=\"f in state.fields\" :key=\"f.field\" :title=\"f.lang ? f.lang.help : null\">\n        <td class=\"label\">\n          {{ f.lang ? f.lang.title : null }}:\n        </td>\n        <td>\n          <wylib-dew :top=\"top\" :ref=\"'dew~'+f.field\" :field=\"f.field\" :state=\"f.styles\" :value=\"data[f.field]\" :values=\"f.values\" :lang=\"f.lang\" @input=\"input\" :bus=\"dewBus\"/>\n        </td>\n      </tr>\n    </table>\n  </div>\n</template>\n\n<script>\nimport Com from './common.js'\nimport Dew from './dew.vue'\nimport InDate from './indate.vue'\n\nexport default {\n  name: 'wylib-mdew',\n  components: {'wylib-dew': Dew, 'wylib-indate': InDate},\n  props: {\n    state:\t{type: Object, default: () => ({})},\n    data:\t{type: Object, default: () => ({})},\n    top:\tnull,\n    bus:\tnull,\n    height:\t{type: Number, default: 300},\t\t//Fixme: used?\n    },\n\n  data() { return {\n    valid:\tnull,\n    dirtys:\t{},\n    userData:\t{},\n    dewBus:\tnew Com.messageBus(this),\n  }},\n\n  computed: {\n    id: function() {return 'mdew_' + this._uid + '_'},\n    dirty: function() {\n      return Object.values(this.dirtys).every(v=>(v))\n    },\n  },\n  \n  methods: {\n    input(value, field, dirty, valid) {\t\t\t//An input has been changed\n      this.$set(this.dirtys, field, dirty)\n      this.valid = valid\n      this.userData[field] = value\n//console.log(\"Mdew input:\", field, value, dirty, valid, this.dirty)\n      this.$emit('input', value, field, this.dirty, valid)\n    },\n  },\n\n  beforeMount: function() {\n    Com.react(this, {fields: []})\n    if (this.bus) this.bus.register(this.id, (msg, data) => {\n      return this.dewBus.notify(msg, data)\t\t//Pass down to children\n    })\n  },\n//  mounted: function() {\n//    this.focus()\n//  }\n}\n</script>\n\n<style>\n  .wylib-mdew table {\n    border-collapse: collapse;\n    width: 100%;\n//    border: 1px solid blue;\n  }\n  .wylib-mdew table .label {\n    text-align: right;\n  }\n  .wylib-mdew table .titles {\n    width: 10%;\n  }\n  .wylib-mdew table tr:nth-child(even) .label {\n    background: #f0f0f0\n  }\n  .wylib-mdew td {\n    border: 1px solid #e8e8e8;\n    white-space: nowrap;\n  }\n</style>\n"],"sourceRoot":""}]);

